body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Raleway', sans-serif; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: silver;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: pink; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

body, h1, h2, h3, h4, h5, h6, body, html {
  font-family: 'Raleway', sans-serif; }

/* Create a Parallax Effect */
.bgimg-1, .bgimg-2, .bgimg-3 {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.bgimg-1 {
  background-image: url(tnathalang.github.io/portfolio/static/media/AbeKislevitz_NightLake_RMBP.b48a2fe1.jpg);
  min-height: 100vh; }

/* Second image (Portfolio) */
.bgimg-2 {
  background-image: url(tnathalang.github.io/portfolio/static/media/sakura.419a7648.jpeg);
  min-height: 400px; }

/* Third image (Contact) */
.bgimg-3 {
  background-image: url(tnathalang.github.io/portfolio/static/media/IMG_0382.4d60e533.jpg);
  min-height: 400px; }

.w3-wide {
  letter-spacing: 10px; }

.w3-hover-opacity {
  cursor: pointer; }

/* Turn off parallax scrolling for tablets and phones */
@media only screen and (max-device-width: 1600px) {
  .bgimg-1, .bgimg-2, .bgimg-3 {
    background-attachment: scroll;
    min-height: 400px; } }

.w3-bar {
  z-index: 9000;
  color: white; }

.mini-intro {
  color: white; }

.mini-intro1 {
  color: black; }

.AboutMe-text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
